/* Text presets */
@font-face {
  font-family: "Outfit";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/outfit-regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Outfit";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/outfit-bold.woff2") format("woff2");
  font-display: swap;
}
html {
  box-sizing: border-box;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

* {
  margin: 0;
}

body {
  height: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  font-family: "Outfit", "Tahoma", sans-serif;
  -webkit-font-smoothing: antialiased;
}

.main {
  min-height: 100%;
  background-color: #d5e1ef;
  display: flex;
  justify-content: center;
  align-items: center;
}

.qr-code {
  max-width: 320px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
  border-radius: 20px;
  padding: 16px;
  padding-bottom: 40px;
}
.qr-code__image {
  margin-bottom: 21px;
}
.qr-code__wrapper {
  text-align: center;
  padding: 0 16px;
}
.qr-code__heading {
  margin-bottom: 16px;
  color: #1f314f;
  font-size: 22px;
  font-weight: 700;
  line-height: 120%;
  letter-spacing: 0;
}
.qr-code__text {
  color: #68778d;
  font-size: 15px;
  font-weight: 400;
  line-height: 140%;
  letter-spacing: 0.2px;
}